@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Varela&display=swap');


.section.s3{
    background-image:linear-gradient(160deg, rgb(7,50,80), rgb(1,23,27));
    overflow:hidden;
}
.section.s3::before{
    position:absolute;
    content:'';
    aspect-ratio:1/1;
    height:470px;
    border-radius:50%;
    background-color:rgba(17,243,194,.788);
    filter:blur(110px);
    top:0;
    left:8%;
    transform:scale(0);
    transition:1s ease-out .2s;
}
.section.s3.active::before{
    transform:scale(1);
}
.section.s3 .bg{
    height:100vh;
    width:50vw;
    position:absolute;
    left:0;
    display:flex;
    justify-content:center;
    align-items:center;
    overflow:hidden;
    transform:translateX(-100%);
    opacity:0;
    transition: .8s ease;
}
.section.s3.active .bg{
    transform:translate(0);
    opacity:1;
}
.section.s3 .bg img{
    height:100%;
    filter:saturate(1.2);
    transform:translateX(-5%);
    
} 
.section.s3.active .intro{
    transform:scale(1);
    opacity:1;
}

.section.s3.active .intro img{
    transform:scale(1);
    
}
.section.s3 .intro{
    position:absolute;
    left:53%;
    margin-top:70px;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:flex-start;
    transform:translateX(-100%);
    opacity:0;
    transition: .8s ease-out .6s;
}
.section.s3 .intro img{
    aspect-ratio:1/1;
    height:500px;
    border-radius:50%;
    border:2px solid rgba(17,250,232,.356);
    transform:scale(0);
    transition:.5s ease 1.2s;
}
.section.s3 .intro h4{
    color: rgb(232,245,248);
    margin-top:45px;
    margin-right:40px;
    align-self:flex-end;
    font-size:17px;
    font-weight:100;
    display:flex;
    justify-content:center;
    align-items:center;
    cursor:pointer;
    font-family:'Varela Rouna', sans-serif;
}
.section.s3 .intro h4 b{
    color:rgb(0,255,191);
    margin: 0 5px;
}
.section.s3 .chevron__arrow{
    color:rgb(0,255,191);
}
.section.s3 .more__games{
    position:absolute;
    display:flex;
    justify-content:flex-start;
    align-items:center;
    height:100%;
    width:100%;
    background-color:rgba(0,0,0.808);
    backdrop-filter:blur(10px);
    transform:scale(0);
    opacity:0;
    z-index:13;
    transform-origin:bottom;
    transition:.2s ease-out
}
.section.s3 .more__games.active{
    transform:scale(1);
    opacity:1;
}
.section.s3 .more__games .back{
    position:fixed;
    top:20px;
    left:20px;
    display:flex;
    justify-content:center;
    align-items:center;
    cursor:pointer;
    z-index:100;
   
}
.section.s3 .more__games .back .chevron__arrow{
    transform:scale(1.3);
    color:cyan;
}
.section.s3 .more__games .games__wrapper{
    position:absolute;
    width:100%;
    height:100%;
    display:flex;
    justify-content:flex-start;
    align-items:center;
    padding: 0 20px;
    gap:20px; 
    margin-top:40px;
    overflow-x:scrol;
    overflow-y:hidden;
}
.section.s3 .more__games .games__wrapper .game{
    position:relative;
    height:580px;
    min-width:380px;
    border-radius:10px;
    border-left:2px solid cyan;
    border-right:2px solid cyan;
    overflow:hidden;
}
.section.s3 .more__games .games__wrapper .game img{
    height:100%;
    width:100%;
    border-radius:10px;
    transition: .3s ease-out;
}
.section.s3 .more__games .games__wrapper .game:hover img{
    transform:scale(1.2);
}
.section.s3 .more__games .games__wrapper .game .link{
    position:absolute;
    height:100%;
    width:100%;
    background-color:rgba(0,0,0.795);
    backdrop-filter:blur(10px);
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    gap:60px;
    opacity:0;
    pointer-events:none;
    transition: .2s ease-out .2s;
    z-index:1;
}
.section.s3 .more__games .games__wrapper .game:hover .link{
    opacity:1;
    pointer-events:all;
}
.section.s3 .more__games .games__wrapper .game .link .logo{
    position:relative;
    display:flex;
    justify-content:center;
    align-items:center;
    
}
.section.s3 .more__games .games__wrapper .game .link .logo img{
    width:90%;
    
}
.section.s3 .more__games .games__wrapper .game .link .btn{
    position:relative;
    display:flex;
    justify-content:center;
    align-items:center;
    background-color:cyan;
    color:black;
    text-transform:uppercase;
    padding:15px 50px;
    border-radius:100px;
    font-size:22px;
    cursor:pointer;
    gap:10px;
}
.section.s3 .more__games .games__wrapper .game .link .btn .chevron__arrow{
    color:black;
}
.section.s3 .more__games .games__wrapper .game .link.game1 .btn{
    background-color:rgba(255,0,0,.589);
}
.section.s3 .more__games .games__wrapper .game .link.game2 .btn{
    background-color:rgb(2248,163,2);
}
.section.s3 .more__games .games__wrapper .game .link.game3 .btn{
    background-color:rgb(255,234,33);
}
.section.s3 .more__games .games__wrapper .game .link.game4 .btn{
    background-color:rgb(238,0,0);
}
.section.s3 .more__games .games__wrapper .game .link.game5 .btn{
    background-color:rgb(0,0,0);
    color:white;
    border:2px solid rgba(255,255,255,.747);
}
.section.s3 
.more__games 
.games__wrapper 
.game .link.game5 
.btn 
.chevron__arrow{
    color:white;
}
