@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Varela&display=swap');

.section.s4{
    position:relative;
    background-image:linear-gradient(160deg, rgb(1,23,27),rgb(10,40,87), rgb(10,120,87) );
    flex-direction:column;
    justify-content:flex-start;
    background-size:400% 400%;
    background-position:100%;
    animation:colors 15s infinite alternate;
}

.section.s4 h1{
    color:rgb(255, 255, 255);
    font-family:'Varela Round', sans-serif;
    font-size:28px;
    display:flex;
    text-transform:uppercase;
    margin-top:30px;
    gap:10px;
    transform:translateY(50px);
    opacity:1;
}
.section.s4.active h1{
    animation: show .3s ease-out 1.1 both;
}
.section.s4 h1 span{
    background-image:linear-gradient(90deg, rgb(118,178,255), rgb(250,122,250));
    font-weight:600;
    font-family: 'Varela Round', sans-serif;
    -webkit-background-clip:text;
    background-clip:text;
    -webkit-text-fill-color:transparent;
}

.section.s4 .streams__container{
    position:relative;
    margin-top:30px;
    width:85%;
    height:80vh;
    display:flex;
    flex-wrap:wrap;
    align-items:center;
    justify-content:space-around;
    
}
.section.s4 .streams__container .stream{
    width:40%;
    height:45%;
    display:flex;
    justify-content:center;
    align-items:center;
    cursor:pointer;
    transition: transform .2s ease-out;
    pointer-events:none;
}
.section.s4 .streams__container .stream img{
    position:absolute;
    width:100%;
    border: 2px solid rgba(230,250,247,.445);
    border-radius:10px;
    height:90%;
}
.section.s4 .streams__container .stream.stream1{
    transform:translateY(-100%);
    opacity:0;
}
.section.s4 .streams__container .stream.stream2{
    transform:translateY(-100%);
    opacity:0;
}
.section.s4 .streams__container .stream.stream3{
    transform:translateY(100%);
    opacity:0;
}
.section.s4 .streams__container .stream.stream4{
    transform:translateY(100%);
    opacity:0;
}
.section.s4.active .streams__container .stream.stream1{
    animation: show .5s ease-out .2s forwards;
    pointer-events:all;
}
.section.s4.active .streams__container .stream.stream2{
    animation: show .5s ease-out .4s forwards;
    pointer-events:all;
}
.section.s4.active .streams__container .stream.stream3{
    animation: show .5s ease-out .6s forwards;
    pointer-events:all;
}
.section.s4.active .streams__container .stream.stream4{
    animation: show .5s ease-out .8s forwards;
    pointer-events:all;
}


