@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Varela&display=swap');


.section.s2{
    margin-top:20vh;
    display:flex;
    justify-content:flex-start;
    align-items:center;
}
.section.s2::before{
    position:absolute;
    content:'';
    aspect-ratio:1/1;
    height:360px;
    border-radius:50%;
    background:rgb(0,255,221);
    filter:blur(110px);
    top:5%;
    right:12%;
    z-index:-1;
    transform:scale(0);
    transform-origin:bottom;
    opacity:0;
}
.section.s2.active::before{
    animation: reveal .5s ease-out forwards;
}
@keyframes reveal{
    to{
        transform:scale(1);
        opacity:1;
    }
}

.section.s2 .container{
    position:absolute;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    left:0;
    width:68%;
    gap:30px;
}
.section.s2 .container h3{
    position:absolute;
    display:flex;
    justify-content:center;
    align-items:center;
    left:0;
    font-size:20px;
    font-weight:100;
    color:rgb(200,227,228, .897);
    font-family:'Varela Round', sans-serif;
    transform:rotate(-90deg) translateY(-100%);
    opacity:0;
    transition: .2s ease-out;
    transition-delay:1.3s;
}

.section.s2.active .container h3{
    transform: rotate(-90deg) translateY(0);
    opacity:1;
}

.section.s2 .container h3::after{
    position:absolute;
    content:'';
    height:1px;
    width:120px;
    background-color:rgb(200,227,228,.897);
    transform:translateX(110%);
}

.section.s2 .container h3::before{
    position:absolute;
    content:'';
    height:1px;
    width:120px;
    background-color:rgb(200,227, 228, .897);
    transform:translateX(-110%);
}
.section.s2 .container .card{
    position:relative;
    display:flex;
    margin: auto 150px;
    transform:translateX(-150px);
    opacity:0;
    transition:.8s ease;
    transition-delay: calc(var(--k) * 120ms);
}
.section.s2.active .container .card{
    transform:translate(0);
    opacity:1
}

.section.s2 .container .card img{
    height:150px;
    min-width:150px;
    max-width:200px;
    border-radius:20px;
    filter:saturate(1.3);
    width:100%;
}

.section.s2 .container .card p{
    display: inline-block;
    color: rgb(150,248, 252);
    font-size:25px;
    margin-left:25px;
    font-weight:500;
    font-family:'Varela Round', sans-serif;
}
.section.s2 .container .card p i{
    font-size:17px;
    color:white;
    font-weight:400;
}
.section.s2 .container .members{
    position:relative;
    align-self:flex-end;
    display:flex;
    justify-content:center;
    align-items:center;
    cursor:pointer;
    margin-right:160px;
    font-size:30px;
}

.section.s2 .container .members span{
    color:rgb(171,237,240);
    font-weight:100;
}
.section.s2 .container .members .arrow-right{
    fill:rgb(164,232,240);
    transform:scale(1.3);
    margin-left:3px;
}

