@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Varela&display=swap');

.section.s1::before{
    position:absolute;
    content:'';
    aspect-ratio:1/1;
    height:360px;
    border-radius:50%;;
    background:rgb(231,0,52);
    filter:blur(130px);
    top:5%;
    right:16%;
    transform:scale(0);
    transform-origin:bottom;
    opacity:0;
}

.section.s1.active:before{
    animation: reveal 1s ease-out 1 forwards;
}
h3{
    position:absolute;
    display:flex;
    justify-content:center;
    align-items:center;
    left:1%;
    font-size:22px;
    font-weight:100;
    color:rgba(200,277,228,.726);
    font-family: 'Varela Round', sans-serif;
    transform:rotate(-90deg);
}
.section.s1 h3::after{
    position:absolute;
    content:'';
    height:1px;
    width:100px;
    background-color:white;
    transform:translateX(100%);
}
.section.s1 h3::before{
    position:absolute;
    content:'';
    height:1px;
    width:100px;
    background-color:white;
    transform:translateX(-100%);
}

.bg__wp{
    position:fixed;
    right:5%;
    height:100vh;
    width:50vw;
    bottom:0;
    background-size:cover;
    background-position:center;
    display:flex;
    justify-content:center;
    align-items:center;
    transform:scale(0);
    transform-origin:bottom;
    opacity:0;
}
.section.s1.active .bg__wp{
    animation: reveal 1s ease-out 1 forwards;
}

@keyframes reveal{
    to{
        transform:scale(1);
        opacity:1;
    }
}
.bg__wp img{
    position:absolute;
    height:100%;
    width:140%;
    right:-10%;
    filter:saturate(1.8);
}
.section.s1 .text__container{
    position:absolute;
    left:10%;
    top:20%;
    max-width:40%;
}

.section.s1 .text__container h1{
    color:rgb(144,253,253);
    font-size:72px;
    line-height:90px;
    font-family:'Roboto', sans-serif;
    transform:translateY(100%);
    opacity:0;
}
.section.s1.active .text__container h1{
    animation:show .9s ease-out 1s 1 forwards;
}
.section.s1 .text__container h2{
    position:relative;
    color:rgb(255,47,248);
    text-transform:uppercase;
    font-size:95px;
    line-height:80px;
    font-family: 'Varela Round', sans-serif;
    font-weight:900;
    transform:translateY(100%);
    opacity:0;
}

.section.s1.active .text__container h2{
    animation:show .5s ease-out 1.3s 1 forwards;
}

.section.s1 .text__container span{
    position:relative;
    display:flex;
    justify-content:flex-start;
    align-items:center;
    flex-wrap: wrap;
    margin: 10px 0 0;
    color:white;
    font-size:17px;
    letter-spacing:1x;
    line-height:20px;
    max-width:70%;
    font-family: 'Roboto', sans-serif;
}
.section.s1 .text__container span p{
    transform:translateX(200%);
    opacity:0;
}
.section.s1.active .text__container span p{
    animation: show 1s ease-out 2s forwards;
    animation-delay:calc(var(--i) * 15ms);
}

.section.s1 .text__container .explore__btn{
    position:relative;
    height:65px;
    width:200px;
    border:2px solid cyan;
    border-radius:100px;
    display:flex;
    justify-content:center;
    align-items:center;
    text-decoration:none;
    margin: 50px 0 0;
    text-transform:capitalize;
    font-size:20px;
    cursor:pointer;
    color:rgb(102,210,243);
    font-weight:900;
    transform:translateY(100%);
    opacity:0;
    transition: .5s ease-out;
}
.section.s1 .text__container .explore__btn:hover{
    background-color:cyan;
    color:black;
    box-shadow: 0 0 70px cyan;
}
.section.s1.active .text__container .explore__btn{
    animation: show 1.3s ease 1.5s forwards;
}

.section.s1 .socials{
    position:fixed;
    bottom:5%;
    right:2%;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    z-index:10;
    transform:translateX(150%);
    opacity:0;
}
.section.s1.active .socials{
    animation: show .3s ease-out 2s forwards
}
.section.s1 .socials::before{
    position:absolute;
    content:'';
    height:120px;
    width:1px;
    background-color:rgba(211,235,241,.445);
    transform:translateY(-154%);
}
.section.s1 .socials .icon{
    position:relative;
    margin:13px 0;
    fill:rgba(255, 255, 255, 0.858);
    transform:scale(1.4);
    cursor:pointer;
}