@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Varela&display=swap');

.section.s5 {
    background-image:linear-gradient(140deg, rgb(33,81,116), rgb(1,16,19));
    z-index:11;
    display:flex;
    flex-direction:column;
    gap:50px;
}
.section.s5::before{
    position:absolute;
    content:'';
    aspect-ratio:1/1;
    height:360px;
    border-radius:50%;
    background-color: #FE0001;
    filter:blur(150px);
    top:5%;
    left:10%;
    transform:scale(0);
    transform-origin:bottom;
    opacity:0;
    pointer-events:none;
}
.section.s5.active::before{
    animation:reveal 1s ease-out 1s forwards;
}
