.section.s5 h1{
    color:rgb(255,255,255,.877);
    font-size:70px;
    font-weight:900;
    font-family:"Varela Round", sans-serif;
    transform:translateY(120px);
    opacity:0;
}
.section.s5.active h1{
    animation:show .6s ease-out .3s forwards;
}
.section.s5 .socials{
    margin-bottom: 120px;
    display:flex;
    column-gap:50px;
}
.section.s5 .socials .icon{
    color:rgb(222,244,245);
    font-size:80px;
    margin:0 40px;
    cursor:pointer;
    transform:translateY(300px);
    opacity:0;
    transition:color .1s ease-out;
}
.section.s5.active .icon{
    animation: show .6s ease-out forwards;
    animation-delay:calc(var(--i) * 300ms);
}
.section.s5 .to-top{
    position:absolute;
    bottom:50px;
    display:flex;
    justify-content:center;
    align-items:center;
    opacity:0;
    transform:translateY(50px);
}
.section.s5.active .to-top{
    animation:show .6s ease-out 1.4s forwards;
}
.section.s5 .to-top a{
    color:white;
    text-decoration:none;
    font-size:18px;
}
.section.s5 .to-top a:hover{
    color:rgb(171,240,240);
}
.section.s5.active .icon:nth-child(1):hover{
    color:rgb(255,0,0);
}
.section.s5.active .icon:nth-child(2):hover{
    color:rgb(122,0,179);
}
.section.s5.active .icon:nth-child(3):hover{
    color:rgb(31,100,179);
}
.section.s5.active .icon:nth-child(4):hover{
    color:rgb(37,51,247);
}
.section.s5.active .icon:nth-child(5):hover{
    color:rgb(255,74,74, .952);
}